h1 {
  text-align: center;
  margin-bottom: 2rem;
  color: #ffcf00;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 400px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #333;
  border-radius: 8px;
  border: 1px solid #ffcf00;
}
form div {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

label {
  font-weight: bold;
  color: #fff;
}

input[type=text],
input[type=password] {
  padding: 0.8rem;
  border: 1px solid #555;
  background-color: #444;
  color: #fff;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}
input[type=text]:focus,
input[type=password]:focus {
  outline: none;
  border-color: #ffcf00;
  box-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
}

button[type=submit] {
  padding: 0.8rem;
  background-color: #ffcf00;
  color: #333;
  border: none;
  border-radius: 4px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}
button[type=submit]:hover {
  background-color: #e6c300;
}

.error-message {
  color: #ff0000;
  text-align: center;
  margin-top: 1rem;
  min-height: 1.2em;
}